  min-height: 100vh;
  width: 100%;
  display: flex;
  align-items: center;
  flex-direction: column;
  text-align: center;
  background: url("@/assets/backgroundLogin.png") no-repeat;
  background-size: cover;
  background-attachment: fixed;

  &-logo {
    width: 150px;
    height: 150px;
    margin-top: 140px;
    filter: drop-shadow(15px -3px 4px rgba(0, 0, 0, 0.2));
    border-radius: 20px;
  }

  &-title {
    font-family: "Dancing Script", cursive;
    font-weight: 500;
    font-size: 50px;
    line-height: 50px;
    color: #fff;
  }

  &-form {
    width: 480px;
    background: rgba(124, 179, 76, 0.432);
    border-radius: 20px;
    display: flex;
    flex-direction: column;
    padding: 40px 0;
    font-family: "Roboto", sans-serif;
    .icon {
      height: 25px;
      color: #cfd7d0;
    }
    &-item {
      max-width: 400px;
      margin: 0 auto;
      display: flex;
      justify-content: center;
      padding-bottom: 10px;
      align-items: center;

      .input {
        width: 350px;
        height: 35px;
        background: rgba(175, 205, 161, 0.39);
        border: none;
        border-bottom: 1px solid #cfd7d0;
        font-size: 18px;
        color: #fff;
        margin-left: 15px;
        padding-left: 5px;
      }
    }
    &-submit {
      width: 120px;
      height: 40px;
      background: linear-gradient(180deg, #9fe318 0%, #0d5c15 100%);
      border: none;
      border-radius: 5px;
      color: #fff;
      font-weight: 600;
      font-size: 15px;
      margin-top: 15px;
      &:active {
        // box-shadow: 0 2px rgb(6, 104, 19);
        transform: translateY(2px);
      }
    }
    &-lotpassword {
      font-size: 14px;
      margin-top: 10px;
      .lotpassword {
        color: #fff;
        .getpassword {
          color: #db490f;
        }
      }
    }
  }
}
</style>
